.main-menu {
  position: fixed; /* Ensure it stays fixed relative to the viewport */
  width: 100vw;
  height: 100vh;
  background-image: url("/public/images/MainMenu/background.svg");
  background-size: cover; /* Covers the entire screen */
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed; /* Ensures it doesn't scroll */
  background-color: black;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
}


.tv-screen {
  width: 47%;
  padding-top: 10%;
  padding-left: 10%;
  /* border: 2px solid red;*/
}

.tv-controls {
  padding-top: 10%;
  display: flex;
  position: relative;
  left: 13%;
  gap: 1rem;
  align-items: flex-start;
  flex-direction: column;
}

button {
  padding: 1rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.help-button {
  position: absolute;
  top: 5vh;
  left: 2vw;
  font-weight: bold;
  font-size: 1rem;
  color: #3a3834;
  background-color: #c7b494;
}

.play-button,
.scoreboard-button {
  width: 50%;
  color: #33302a;
  background-color: #c7b494;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.game-title {
  /* position: absolute;
  left: 10vw;  Place title relative to viewport width
  top: 15vh; Place title relative to viewport height */
  font-size: 107px; /* Scales proportionally with screen width */
  font-family: "Caesar Dressing", system-ui;
  color: #c7b494;
}

@media (max-width: 768px) {
  .game-title {
    font-size: 5vw; /* Adjust font size for smaller screens */
    left: 5vw;
    top: 10vh;
  }

  .help-button {
    top: 3vh;
    left: 1vw;
  }

  /* .play-button,
  .scoreboard-button {
      width: 15vw; /* Make buttons larger on small screens
  } */
}

button:hover {
  transform: scale(1.1); /* Slightly increase size on hover */
  background-color: #332206; /* Change background color */
  color: #ffffff; /* Change text color */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow */
}

/* Specific styles for buttons */
.help-button:hover {
  background-color: #5a4233; /* Custom hover color for help button */
}

.play-button:hover,
.scoreboard-button:hover {
  background-color: #6b4e3d; /* Custom hover color for play and scoreboard buttons */
}

